/*
Variable declarations
 */
:root {
	--main-bg-color: #209cee;
}

* {
	margin: 0;
	padding: 0;
}

html,
body {
	background-color: #fff;
	font-size: 1rem;
	font-family: -apple-system, BlinkMacSystemFont, sans-serif;
	height: 100vh;
	width: 100vw;
}

a {
	text-decoration: none;
}

section {
	padding: 1.5rem 3rem;
}

.container {
	margin: 0 auto;
	position: relative;
}

.subtitle {
	color: var(--main-text-color);
	font-size: 1.3rem;
}

.feeds {
	margin-top: 1.5rem;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
}

.feeds .feed {
	margin: 12px;
	height: 230px;
	min-width: 0;
}

.pagination {
	margin-left: 12px;
	justify-content: center;
	margin-top: 2rem;
}

.sort {
	display: flex;
	align-items: center;
}

.sort .flex-item {
	margin-bottom: 0;
	flex: 1 1;
}

.ReactTable .rt-th, .ReactTable .rt-td {
	padding: 14px 10px;
}

.ReactTable .rt-thead .rt-th, .ReactTable .rt-thead .rt-td {
	padding: 14px 10px;
	text-align: left;
	font-weight: 500;
}

.react-t {
	margin-top: 1rem;
}

.feed-boxes {
	margin-bottom: 2rem;
}
/* 
  ##Device = iMac etc.,
  ##Screen = 1472px and above.
*/

@media (min-width: 1357px) {
	/* For desktop: */
	.container {
		max-width: 1344px;
		width: 1344px;
	}
}

/* 
  ##Device = Tablets, Ipads (portrait)
  ##Screen = 768px to 1024px
*/

@media (min-width: 768px) and (max-width: 1356px) {
	.feeds {
		grid-template-columns: repeat(2, 1fr);
	}
	.feeds .feed {
		height: 450px;
	}
	.container .select-boxes div:last-child {
		text-align: right;
	}
}

/* 
  ##Device = Low Resolution Tablets, Mobiles (Landscape)
  ##Screen = 481px to 767px
*/

@media (min-width: 481px) and (max-width: 767px) {
	.feeds {
		grid-template-columns: repeat(1, 1fr);
	}
	.container .select-boxes {
		flex-direction: column;
	}
	.container .select-boxes div:first-child {
		margin-bottom: 1.5rem;
	}
	.container .select-boxes div:last-child {
		text-align: left;
	}
}

/* 
  ##Device = Most of the Smartphones Mobiles (Portrait)
  ##Screen = 320px to 479px
*/

@media (min-width: 320px) and (max-width: 480px) {
	.feeds {
		grid-template-columns: repeat(1, 1fr);
	}
	.feeds .feed {
		height: 460px;
	}
}

